var pageSlider = new Swiper('.layout-slider__container', {
    slidesPerView: 1,
    spaceBetween: 0,
    direction: 'vertical',
    parallax: true,
    speed: 500,
    init: false,
    keyboard: {
        enabled: true,
        onlyInViewport: true,
        pageUpDown: true
    },
    mousewheel: {
        invert: false,
    },
    observer: true,
    observeParents: true,
    observeSlideChildren: true,
    allowTouchMove: false,
    pagination: {
        el: '.layout-slider__pagination',
        type: 'bullets',
        clickable: true,
    },
})

const resizeHandlerSlider = () => {
    if (window.innerWidth > 992) {
        pageSlider.init()
    }
}

resizeHandlerSlider()

$(".film-item__next").click(function () {
    pageSlider.slideNext()
})


window.addEventListener('resize', resizeHandlerSlider);



$("._toggle-burger-menu").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})
