function filmsSliders() {
    let SlidersArray = []
    $(".films-slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.films-slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            nested: true,
            mousewheel: {
                invert: false,
            },
            on: {
                init: function () {
                    setActiveFilm($(el),this.activeIndex)
                },
                slideChangeTransitionEnd: function () {
                    setActiveFilm($(el),this.activeIndex)
                },
            },
        })
    })


    $('.films-slider').bind('mousewheel', function(e){
        e.preventDefault()
    });

    function setActiveFilm(swiper,activeSlideTab) {
        $(swiper).parents(".films").find(".film-detail").removeClass("_active")
        $(swiper).parents(".films").find(`.film-detail[data-tab="${activeSlideTab+1}"]`).addClass("_active")
    }
}

function actorSlider() {
    var swiper = new Swiper('.actor-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        nested: true,
        slideToClickedSlide: true,
        mousewheel: {
            invert: false,
        },
        on: {
            init: function () {
                setActiveActor($('.actor-slider'),this.activeIndex)
            },
            slideChangeTransitionEnd: function () {
                setActiveActor($('.actor-slider'),this.activeIndex)
            },
        },
    })

    function setActiveActor(swiper,activeSlideTab) {
        $(swiper).parents(".actors").find(".actor-detail").removeClass("_active")
        $(swiper).parents(".actors").find(`.actor-detail[data-tab="${activeSlideTab+1}"]`).addClass("_active")
    }
}

filmsSliders()
actorSlider()
